@import "/src/styles/variables";

.wrapper {
    padding-inline: 106px;
}
.message {
    display: flex;
    flex-direction: column;
    padding: 16px 18px 18px 21px;
    width: 416px;
    height: 172px;
    border: 1px solid #EDEDED;
    border-radius: 8px;
    box-shadow: 0px 3px 10px 0px #00000014;
    background: $white;
    position: absolute;
    z-index: 1;
    top: 608px;


    &-header {
        display: flex;
        align-items: center;
    }

    &-icon {
        width: 39px;
        height: 39px;
        margin-right: 14px;
    }

    &-name {
        margin-right: 20px;
        font-family: "Sofia Sans", sans-serif;
        font-weight: 400;
        font-size: 15px;
        line-height: 23px;
        letter-spacing: 0.45px;
        color: $main-text;
    }

    &-title {
        margin-left: 5px;
        font-family: "Sofia Sans", sans-serif;
        font-weight: 400;
        font-size: 11px;
        line-height: 20px;
        letter-spacing: 0.22px;
        color: $main-text; 
    }

    &-description {
        font-family:  "Sofia Sans", sans-serif;
        font-weight: 400;
        font-size: 12px;
        line-height: 23px;
        letter-spacing: 0.48px;
        color: $main-text;
    }
}